import React, { useState, useEffect } from 'react'
import Web3 from 'web3'
import { useTheme } from "../../context/ThemeContext"
import info from "../../assets/svg/button/info.svg"
import infoBlack from "../../assets/svg/button/infoBlack.svg"

export default function Wallet() {
  const { theme } = useTheme()
  const [walletAddress, setWalletAddress] = useState('') //    
  const [isMetamaskInstalled, setIsMetamaskInstalled] = useState(false) //    Metamask

  //   Metamask  Web3 
  useEffect(() => {
    const ethereum = (window as any).ethereum //  
    if (ethereum) {
      setIsMetamaskInstalled(true) // Metamask 
    } else {
      setIsMetamaskInstalled(false) // Metamask  
    }
  }, [])

  //     Metamask
  const connectWallet = async () => {
    const ethereum = (window as any).ethereum //  
    if (ethereum) {
      const web3 = new Web3(ethereum) //  Web3   Metamask
      try {
        //    
        const accounts = await ethereum.request({ method: 'eth_requestAccounts' })
        const address = accounts[0]

        //    
        const shortAddress = `${address.slice(0, 7)}...${address.slice(-5)}`
        setWalletAddress(shortAddress) //     
      } catch (error) {
        console.error('   Metamask:', error)
      }
    } else {
      //  Metamask  ,    
      window.open('https://metamask.io/download.html', '_blank')
    }
  }

  return (
    <button
      type="button"
      onClick={connectWallet} //    connectWallet  
      className="relative font-sans font-bold text-black-50 dark:text-white text-base xl:text-lg h-[47px] lg:h-[50px] xl:h-16 rounded-2xl w-[310px] flex items-center justify-center bg-white dark:bg-black-50"
    >
      {walletAddress ? walletAddress : ' '}
      <img className="ml-4" src={theme ? info : infoBlack} alt="info" />
    </button>
  )
}
